.menu-m-panel {
  position: fixed;
  z-index: 201;
  background-color: #009EE3;
  height: 100%;
  width: 100%;
  top: -100%;
  left: 0;
  padding: 0 15px 35px;
  transition: all .25s ease-in-out;
  overflow: auto;
  box-sizing: border-box;
}

.menu-m-panel.open {
  top: 0;
}

.menu-m-panel__hd {
  display: flex;
  justify-content: space-between;
  height: 72px;
  align-items: center;
  position: relative;
}